.settingCheckbox {
    display: flex;
    justify-content: space-between;
    gap: 80px;
    align-items: flex-start;
}

.settingCheckbox span {
    font-size: 1.5rem;
}

.settingCheckbox input {
    transform: scale(3);
    position: relative;
    top: 10px;
    right: 14px;
    cursor: pointer;
}